<template>
  <div class="body">
    <ul>
      <li v-for="(item,index) in items" :key="item.id">
        <router-link :to="item.path"  class="link" :class="{'change':titleSelect==index}"  @click.native="tabClass(index)">
           <div class="img-list" id="img-list">
        <img :src="item.url" >
        <div class="info">{{item.title}}</div>
        <div class="info">{{item.date}}</div>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'GIF1',
  data () {
    return {
      items: [
        {
          id: '1',
          title: '#1',
          date: '2020/05/03-05/04',
          url: require('../assets/img/1.gif'),
          path: '/GIF1'
        }, {
          id: '2',
          title: '#2',
          date: '2020/05/04-05/05',
          url: require('../assets/img/2.gif'),
          path: '/GIF2'
        }, {
          id: '3',
          title: '#3',
          date: '2020/05/04-05/05',
          url: require('../assets/img/3.gif'),
          path: '/GIF3'
        }, {
          id: '4',
          title: '#4',
          date: '2020/05/06-05/07',
          url: require('../assets/img/4.gif'),
          path: '/GIF4'
        }
      ],
      titleSelect: 0
    }
  },
  components: {},
  methods: {
    tabClass (index) {
      this.titleSelect = index
    }
  }
}
</script>

<style scoped>
.body{
  width: 10vw;
  height: 85vh;
  margin-left: 4vw;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2vh;
}
img{
  width: 5vw;
  padding: 1vh;
  margin-left: 2.5vw;
}
li{
  margin: 0 2vw 0 0;
}
.link{
  text-decoration:none;
   color: gray;
}
.img-list{
  cursor: pointer;
  width: 8vw;
}
.img-list:hover{
  color: white;
  transition: .25s;
}
.info{
  font-family: "REGULAR";
  font-size: .5em;
  text-align: center;
  transform: scale(0.7) translateX(30px);
}
.change{
  color: white;
}
</style>



