<template>
    <div id="lunbobox">
      <router-link :to="path2" class="link">
      <div class="enter one" >{{enter}}</div>
      </router-link>
      <router-link :to="path" class="link">
        <div class="enter two">{{enter}}</div>
        </router-link>
        <div class="lunbo">
            <div class="imge"><img :src="url"></div>
            <div class="imge"><img :src="url1"></div>
            <div class="imge"><img :src="url2"></div>
            <div class="imge"><img :src="url3"></div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      enter: 'enter',
      url: require('../assets/img/index/01.png'),
      url1: require('../assets/img/index/02.png'),
      url2: require('../assets/img/index/03.png'),
      url3: require('../assets/img/index/04.png'),
      path: '/video',
      path2: '/Guo'
    }
  },
  mounted () {
    let t
    let index = 0
    t = setInterval(play, 800)
    function play () {
      index++
      if (index > 4) {
        index = 0
      }
      $('.lunbo imge').eq(index).show().siblings().hide()
    }
    $('.lunbo .imge img ').hover(
      function () {
        clearInterval(t)
      },
      function () {
        t = setInterval(play, 800)
        function play () {
          index++
          if (index > 4) {
            index = 0
          }
          $('.lunbo .imge').eq(index).show().siblings().hide()
        }
      })
  },
  methods: {

  }
}
</script>

<style>
#lunbobox {
  width:100vw;
  height:100vh;
  position:relative;
}
.lunbo {
    width:100vw;
    height:100vh;
}
.lunbo img {
    width:100vw;
    height:100vh;
  position:absolute;
top:0px;
left:0px;
}
.enter {
   font-family: 'Light';
    width:120px;
    height:50px;
    font-size:1em;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(139, 9, 19);
    position:absolute;
    border-radius: 25px;
    cursor:pointer;
    z-index:99;
    border: solid 1px rgba(139, 9, 19, 0);
    transition: .5s;
}
.enter:hover{
        border: solid 3px rgb(139, 9, 19, 1);
        transition: .5s;
}
.one{
    bottom: 5vh;
    left:20vw;
}
.two{
    bottom: 5vh;
    right:20vw;
}
.link{
  z-index: 100;
}
</style>



