import Vue from 'vue'
import Router from 'vue-router'
import Photo from '@/components/Photo'
import GIF1 from '@/components/GIF1'
import Content from '@/components/Content'
import index from '@/components/index'
import tag1 from '@/components/tag_1'
import tag2 from '@/components/tag_2'
import tag3 from '@/components/tag_3'
import tag4 from '@/components/tag_4'
import header from '@/components/header'
import Guo from '@/components/Guo'
import Gheader from '@/components/Gheader'
import streetview from '@/components/streetview'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      components: {
        photo: index
      }
    },
    {
      path: '/video',
      name: 'video',
      components: {
        header: header,
        photo: Photo,
        content: Content
      }
    },
    {
      path: '/GIF1',
      name: 'GIF1',
      components: {
        header: header,
        photo: GIF1,
        content: tag1
      }
    },
    {
      path: '/GIF2',
      name: 'GIF2',
      components: {
        header: header,
        photo: GIF1,
        content: tag2
      }
    },
    {
      path: '/GIF3',
      name: 'GIF3',
      components: {
        header: header,
        photo: GIF1,
        content: tag3
      }
    },
    {
      path: '/GIF4',
      name: 'GIF4',
      components: {
        header: header,
        photo: GIF1,
        content: tag4
      }
    },
    {
      path: '/Guo',
      name: 'Guo',
      components: {
        header: Gheader,
        photo: Guo
      }
    },
    {
      path: '/streetview',
      name: 'streetview',
      components: {
        header: Gheader,
        photo: streetview
      }
    }
    // {
    //   path: '/about',
    //   name: 'about',
    //   components: {
    //     header:Header,
    //     home:About
    //   }
    // }
  ]
})



