<template>
  <div class="body">
    <div class="img weibo">
    <img :src="url14" alt=""  width="450">
    </div>
    <div class="img baidu">
    <img :src="url8" alt=""  width="225">
    </div>
    <div class="img zhifubao">
    <img :src="url16" alt=""  width="143">
    </div>
    <div class="img gaode">
    <img :src="url9" alt=""  width="160">
    </div>
    <div class="img taobao">
    <img :src="url12" alt=""  width="220">
    </div>
    <div class="img time">
    <img :src="url11" alt=""  width="150">
    </div>
    <div class="img weixin">
    <img :src="url15" alt=""  width="460">
    </div>
    <div class="img tengxunhuiyi">
    <img :src="url13" alt=""  width="160">
    </div>
    <div class="img tengxunhuiyi">
    <img :src="url13" alt=""  width="160">
    </div>
    <div class="img ins">
    <img :src="url10" alt=""  width="380">
    </div>
    <img :src="url" alt="" class="gif one">
    <img :src="url1" alt="" class="gif two">
    <img :src="url2" alt="" class="gif three">
    <img :src="url3" alt="" class="gif four">
    <img :src="url4" alt="" class="gif five">
    <img :src="url5" alt="" class="gif six">
    <img :src="url6" alt="" class="gif seven">
    <img :src="url7" alt="" class="gif eight">
  </div>
</template>

<script>
export default {
  name: 'tag_1',
  data () {
    return {
      url: require('../assets/img/GIF/1.1.gif'),
      url1: require('../assets/img/GIF/1.2.gif'),
      url2: require('../assets/img/GIF/1.3.gif'),
      url3: require('../assets/img/GIF/1.4.gif'),
      url4: require('../assets/img/GIF/1.5.gif'),
      url5: require('../assets/img/GIF/1.6.gif'),
      url6: require('../assets/img/GIF/1.7.gif'),
      url7: require('../assets/img/GIF/1.8.gif'),
      url8: require('../assets/img/tag1/baidu.png'),
      url9: require('../assets/img/tag1/gaode.png'),
      url10: require('../assets/img/tag1/ins.png'),
      url11: require('../assets/img/tag1/time.png'),
      url12: require('../assets/img/tag1/taobao.png'),
      url13: require('../assets/img/tag1/tengxunhuiyi.png'),
      url14: require('../assets/img/tag1/weibo.png'),
      url15: require('../assets/img/tag1/weixin.png'),
      url16: require('../assets/img/tag1/zhifubao.png')
    }
  },
  components: {

  },
  mounted () {
    $('.weibo').hover(function () {
      $('.one').css('display', 'block')
    }, function () {
      $('.one').css('display', 'none')
    })
    $('.baidu').hover(function () {
      $('.two').css('display', 'block')
    }, function () {
      $('.two').css('display', 'none')
    })
    $('.zhifubao').hover(function () {
      $('.three').css('display', 'block')
    }, function () {
      $('.three').css('display', 'none')
    })
    $('.gaode').hover(function () {
      $('.four').css('display', 'block')
    }, function () {
      $('.four').css('display', 'none')
    })
    $('.taobao').hover(function () {
      $('.five').css('display', 'block')
    }, function () {
      $('.five').css('display', 'none')
    })
    $('.ins').hover(function () {
      $('.six').css('display', 'block')
    }, function () {
      $('.six').css('display', 'none')
    })
    $('.weixin').hover(function () {
      $('.seven').css('display', 'block')
    }, function () {
      $('.seven').css('display', 'none')
    })
    $('.tengxunhuiyi').hover(function () {
      $('.eight').css('display', 'block')
    }, function () {
      $('.eight').css('display', 'none')
    })
  }
}
</script>

<style scoped>
.body{
  margin-left: 20px;
  margin-top: 17px;
  margin-right: 5vw;
  padding: 12px 30px 12px 30px;
  border: solid 3px black;
  width: 75vw;
  border-radius: 10px;
  display: flex;
  position: relative;
}
.gif{
  position:absolute;
  width: 20vw;
  display: none;
  border-radius: 10px;
}
.one{top:-10vh;left:10vw;}
.two{top:-10vh;left:30vw;}
.three{top:-10vh;right:10vw;}
.four{top:-10vh;right:1vw;}
.six{bottom:1vh;left:1vw;}
.seven{bottom:1vh;left:40vw;}
.eight{bottom:1vh;right:1vw;}
.img{
  position: absolute;
}
.weibo{
  left:2vw;
}
.baidu{
  left:34.5vw;
}
.zhifubao{
  right: 14vw;
}
.gaode{
  right:2vw;
}
.taobao{
  top:30vh;
}
.time{
  top:33vh;
  left:18vw;
}
.weixin{
  top:32vh;
  left:29vw;
}
.tengxunhuiyi{
  bottom:2vh;
  right:2vw;
}
.ins{
  bottom: 0vh;
}
</style>



