<template>
  <div class="body">
    <div class="img weibo">
    <img :src="url15" alt="" width="400">
    </div>
    <div class="img wangyi">
    <img :src="url12" alt="" width="400">
    </div>
    <div class="img wangzheyingdi">
    <img :src="url14" alt="" width="190">
    </div>
    <div class="img weixin">
    <img :src="url16" alt="" width="290">
    </div>
    <div class="img time">
    <img :src="url11" alt="" width="400">
    </div>
    <div class="img elme">
    <img :src="url9" alt="" width="190">
    </div>
    <div class="img bilibili">
    <img :src="url8" alt="" width="240">
    </div>
    <div class="img hupu">
    <img :src="url10" alt="" width="290">
    </div>
    <div class="img wangzhe">
    <img :src="url13" alt="" width="440">
    </div>
    <img :src="url" alt="" class="gif one">
    <img :src="url1" alt="" class="gif two">
    <img :src="url2" alt="" class="gif three">
    <img :src="url3" alt="" class="gif four">
    <img :src="url4" alt="" class="gif five">
    <img :src="url5" alt="" class="gif six">
    <img :src="url6" alt="" class="gif seven">
    <img :src="url7" alt="" class="gif eight">
  </div>
</template>

<script>
export default {
  name: 'tag_2',
  data () {
    return {
      url: require('../assets/img/GIF/2.1.gif'),
      url1: require('../assets/img/GIF/2.2.gif'),
      url2: require('../assets/img/GIF/2.3.gif'),
      url3: require('../assets/img/GIF/2.4.gif'),
      url4: require('../assets/img/GIF/2.5.gif'),
      url5: require('../assets/img/GIF/2.6.gif'),
      url6: require('../assets/img/GIF/2.7.gif'),
      url7: require('../assets/img/GIF/2.8.gif'),
      url8: require('../assets/img/tag2/bilibili.png'),
      url9: require('../assets/img/tag2/elme.png'),
      url10: require('../assets/img/tag2/hupu.png'),
      url11: require('../assets/img/tag2/time.png'),
      url12: require('../assets/img/tag2/wangyiyun.png'),
      url13: require('../assets/img/tag2/wangzhe.png'),
      url14: require('../assets/img/tag2/wangzheyingdi.png'),
      url15: require('../assets/img/tag2/weibo.png'),
      url16: require('../assets/img/tag2/weixin.png')
    }
  },
  components: {

  },
  mounted () {
    $('.weibo').hover(function () {
      $('.one').css('display', 'block')
    }, function () {
      $('.one').css('display', 'none')
    })
    $('.wangyi').hover(function () {
      $('.two').css('display', 'block')
    }, function () {
      $('.two').css('display', 'none')
    })
    $('.wangzheyingdi').hover(function () {
      $('.three').css('display', 'block')
    }, function () {
      $('.three').css('display', 'none')
    })
    $('.elme').hover(function () {
      $('.four').css('display', 'block')
    }, function () {
      $('.four').css('display', 'none')
    })
    $('.weixin').hover(function () {
      $('.five').css('display', 'block')
    }, function () {
      $('.five').css('display', 'none')
    })
    $('.bilibili').hover(function () {
      $('.six').css('display', 'block')
    }, function () {
      $('.six').css('display', 'none')
    })
    $('.hupu').hover(function () {
      $('.seven').css('display', 'block')
    }, function () {
      $('.seven').css('display', 'none')
    })
    $('.wangzhe').hover(function () {
      $('.eight').css('display', 'block')
    }, function () {
      $('.eight').css('display', 'none')
    })
  }
}
</script>

<style scoped>
.body{
  margin-left: 20px;
  margin-top: 17px;
  margin-right: 5vw;
  padding: 12px 30px 12px 30px;
  border: solid 3px black;
  width: 75vw;
  border-radius: 10px;
  display: flex;
  position: relative;
}
.gif{
  position:absolute;
  width: 20vw;
  display: none;
  border-radius: 10px;
}
.one{top:-10vh;left:10vw;}
.two{top:-10vh;left:40vw;}
.three{top:-10vh;right:1vw;}
.four{top:10vh;right:1vw;}
.five{top:40vh;left:1vw;}
.six{top:30vh;left:17vw;}
.seven{top:50vh;left:17vw;}
.eight{top:30vh;right:10vw;}
.img{
  position: absolute;
}
.wangyi{
  left: 31vw;
}
.wangzheyingdi{
  right:2vw;
}
.weixin{
  bottom:-3vh;
}
.time{
  top: 20vh;
  left: 31.5vw;
}
.elme{
  right:2vw;
  top:20vh;
}
.bilibili{
  left:23vw;
  bottom:25vh
}
.hupu{
  left:23vw;
  bottom:-2vh;
}
.wangzhe{
  bottom:3vh;
  right:1vw;
}
</style>



