<template>
  <div>
    <header id="header">
      <router-link :to="path" class="link">
        <span class="tag-1" id="tag-1"><div class="point"></div>{{text1}}</span>
         </router-link>
        <div class="time">
           <div ref="startTimer">00:00:00</div>
        </div>
        <span class="tag-2">{{text2}}</span>
        <span class="tag-3">{{text3}}</span>
        <span class="tag-4">{{text4}}</span>
    </header>
  </div>
</template>

<script>
export default {
  name: 'header',
  props: {
    time: {
      type: Number
    }
  },
  data () {
    return {
      text1: '过 曝 →',
      text2: 'screen time',
      text3: 'tags↓',
      text4: 'videos ↵',
      timer: '',
      hour: 0,
      minutes: 0,
      seconds: 0,
      cr: '',
      path: '/index'
    }
  },
  created () {
    this.timer = setInterval(this.startTimer, 1000)
  },
  methods: {
    startTimer () {
      this.seconds += 1
      if (this.seconds >= 60) {
        this.seconds = 0
        this.minutes = this.minutes + 1
      }

      if (this.minutes >= 60) {
        this.minutes = 0
        this.hour = this.hour + 1
      }
      this.$refs.startTimer.innerHTML = (this.hour < 10 ? '0' + this.hour : this.hour) + ':' + (this.minutes < 10 ? '0' + this.minutes : this.minutes) + ':' + (this.seconds < 10 ? '0' + this.seconds : this.seconds)
      this.cr = this.seconds
    },
    stop () {
      clearInterval(this.timer)
    },
    start () {
      this.timer = setInterval(this.startTimer, 1000)
    }
  }
}
</script>

<style scoped>
#header{
  width: 90vw;
  height: 7vh;
  margin: 1px auto;
  font-family: "Light";
  font-size: 1.1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: solid black 1px;
  position: flex;
  top:0;
}
.tag-1{
  position: relative;
  left:20px;
}
.point{
  position: absolute;
  left:-30px;
  width: 20px;
  height: 20px;
  background-color:black;
  border-radius: 50%;
}
span{
  color: #000;
  text-decoration: none;
}
.link{
   text-decoration: none;
}
.time{
  margin-left: -18vw;
}
</style>



