<template>
    <div id="lunbobox">
      <div class="back">
          <img :src="url" width="1400">
      </div>
      <router-link :to="path" class="link">
      <div class="know">
           <img :src="url5" width="200" class="imgg">
      </div>
      </router-link>
        <div class="lunbo">
            <div class="imge"><img :src="url1"></div>
            <div class="imge"><img :src="url2"></div>
            <div class="imge"><img :src="url3"></div>
            <div class="imge"><img :src="url4"></div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Guo',
  data () {
    return {
      url: require('../assets/img/jiankong.png'),
      url1: require('../assets/img/jiankong/1.png'),
      url2: require('../assets/img/jiankong/2.png'),
      url3: require('../assets/img/jiankong/3.png'),
      url4: require('../assets/img/jiankong/4.png'),
      url5: require('../assets/img/konw.png'),
      path: '/streetview'
    }
  },
  mounted () {
    let t
    let index = 0
    t = setInterval(play, 800)
    function play () {
      index++
      if (index > 4) {
        index = 0
      }
      $('.lunbo imge').eq(index).show().siblings().hide()
    }
    $('.imgg').hover(
      function () {
        clearInterval(t)
      },
      function () {
        t = setInterval(play, 800)
        function play () {
          index++
          if (index > 4) {
            index = 0
          }
          $('.lunbo .imge').eq(index).show().siblings().hide()
        }
      })
  }
}
</script>

<style scoped>
#lunbobox {
  width:100vw;
  height:100vh;
  position:relative;
}
.lunbo {
    width:35vw;
    height:35vh;
}
.lunbo img {
    width:35vw;
    height:35vh;
    position:absolute;
    top:25vh;
    left:35vw;
}
.back{
  position: absolute;
  top:-8vh;
}
.know{
  position: absolute;
  bottom: 20vh;
  left: 45vw;
}
.imgg{
  z-index: 99;
}
</style>



